/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react";
import Helmet from "react-helmet";

import { withConfig } from "../../api/config";
import GoogleAnalytics from "./googleAnalytics";

function Head(props) {
  const { description, lang, meta, keywords, title, author, siteTitle } = props;
  const metaDescription = description;

  return (
    <React.Fragment>
      <Helmet
      htmlAttributes={{
        lang,
      }}
      title={title}
      defaultTitle={siteTitle}
      titleTemplate={`${siteTitle} - %s`}
      meta={[
        {
          name: `description`,
          content: metaDescription,
        },
        {
          property: `og:title`,
          content: title,
        },
        {
          property: `og:description`,
          content: metaDescription,
        },
        {
          property: `og:type`,
          content: `website`,
        },
        {
          name: `twitter:card`,
          content: `summary`,
        },
        {
          name: `twitter:creator`,
          content: author,
        },
        {
          name: `twitter:title`,
          content: title,
        },
        {
          name: `twitter:description`,
          content: metaDescription,
        },
      ]
        .concat(
          keywords.length > 0
            ? {
                name: `keywords`,
                content: keywords.join(`, `),
              }
            : []
        )
        .concat(meta)}
    >
      <link href="https://fonts.googleapis.com/css?family=Lato:300,300i,400,400i,700,700i|Open+Sans" rel="stylesheet" />
      <style type="text/css">
      {`
        body {
          font-family: 'Lato', 'Open Sans', 'Noto Sans SC', "Microsoft YaHei", sans-serif;
        }
      `}
      </style>

    </Helmet>
      <GoogleAnalytics />
    </React.Fragment>
    
  );
}

Head.defaultProps = {
  lang: `en`,
  meta: [],
  keywords: [],
  description: ``,
};

export default withConfig((props) => {
  const siteTitle = props.config.title;
  const author = props.config.author;
  const description = props.config.description;
  return <Head siteTitle={siteTitle} author={author} description={description} {...props} />
});
